import React, { useEffect } from "react";
import { observer } from "mobx-react";

import { useStores } from "@/hooks";

const Loader = observer(({ recognition }: any) => {
  const { event } = useStores();

  const startListen = () => {
    return recognition.startListening({ language: "ko" });
  };

  const keepListen = setTimeout(() => {
    recognition.stopListening();

    event.isClicked = false;
  }, 2000);

  useEffect(() => {
    startListen();
  }, []);

  useEffect(() => {
    keepListen;
    return () => {
      clearTimeout(keepListen);
    };
  }, [keepListen]);

  return (
    <>
      <div className="google-loader">
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </div>
    </>
  );
});

export default Loader;
try {
    // @ts-ignore
    Loader.displayName = "Loader";
    // @ts-ignore
    Loader.__docgenInfo = { "description": "", "displayName": "Loader", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/organisms/Loader/index.tsx#Loader"] = { docgenInfo: Loader.__docgenInfo, name: "Loader", path: "src/components/organisms/Loader/index.tsx#Loader" };
}
catch (__react_docgen_typescript_loader_error) { }