import React from "react";
import { observer } from "mobx-react";
import classNames from "classnames";

import { IButton } from "@/interface";
import { useStores } from "@/hooks";

const Button = observer(
  ({ prop, children, style }: IButton): React.ReactElement => {
    const { event } = useStores();

    return (
      <>
        <button
          className={classNames("Button", style)}
          onClick={prop ? prop && event.onClick : event.onClick}
        >
          {children}
        </button>
      </>
    );
  }
);

export default Button;
try {
    // @ts-ignore
    Button.displayName = "Button";
    // @ts-ignore
    Button.__docgenInfo = { "description": "", "displayName": "Button", "props": {} };
    // @ts-ignore
    if (typeof STORYBOOK_REACT_CLASSES !== "undefined")
        // @ts-ignore
        STORYBOOK_REACT_CLASSES["src/components/atoms/Button/index.tsx#Button"] = { docgenInfo: Button.__docgenInfo, name: "Button", path: "src/components/atoms/Button/index.tsx#Button" };
}
catch (__react_docgen_typescript_loader_error) { }